                      <rect x="3" y="4" width="18" height="6" rx="1" ry="1" />
                      <!--  -->
                      <rect x="6" y="12" width="18" height="6" rx="1" ry="1" />
                    </svg>
                    <span class="overlay-count">+{{ post.imgs.length - 3 }}</span>
