/*    btn-ghost  */
.upload-fab {
  position: absolute;
  bottom: 10px;
  left: 10px;
  width: 28px;
  /*   */
  height: 28px;
  border-radius: 50%;
  background: var(--card-light);
  border: var(--glass-border);
  backdrop-filter: blur(calc(var(--blur)/2));
  color: var(--text-light);
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: background .25s, transform .15s;
  box-shadow: 0 2px 6px rgba(0, 0, 0, .15);
}

