import { createRouter, createWebHashHistory } from 'vue-router';

/* =====   ===== */
import MainApp from '../views/MainApp.vue'; // //
import MomentsPage from '../views/MomentsPage.vue'; //  + 
import SettingsAccount from '../views/settings/SettingsAccount.vue'; // 
import SettingsAI from '../views/settings/SettingsAI.vue'; // AI
import SettingsLayout from '../views/settings/SettingsLayout.vue'; // 
import SettingsVisual from '../views/settings/SettingsVisual.vue'; // 

const routes = [
  {
    path: '/',
    component: MainApp,
    children: [
      //    
      { path: '', component: MomentsPage },

      //   
      {
        path: 'settings',
        component: SettingsLayout,
        children: [
          { path: '',        redirect: 'visual' },
          { path: 'visual',  component: SettingsVisual  },
          { path: 'account', component: SettingsAccount },
          { path: 'ai',      component: SettingsAI      }
        ]
      }

      // 
      // { path: 'test', component: UploadTest }
    ]
  }
];

const router = createRouter({
  history: createWebHashHistory(),  // Hash  GH Pages
  routes
});

export default router;
